
'use client';

import type { ReactNode } from 'react';
import { createContext, useState, useEffect, useCallback } from 'react';
import {
  getAuth,
  onAuthStateChanged,
  signOut,
  signInWithPopup,
  GoogleAuthProvider,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  sendPasswordResetEmail,
  updateProfile,
  type User,
} from 'firebase/auth';
import { firebaseApp } from '@/lib/firebase';
import { doc, setDoc, getDoc, serverTimestamp, getFirestore, type Firestore, collection, getDocs, query, where, arrayUnion } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL, getStorage, type FirebaseStorage, deleteObject } from 'firebase/storage';
import type { ProfileData, TestResult, SignupData, UserRole } from '@/types';

interface AuthContextType {
  user: User | null;
  profile: ProfileData | null;
  login: (email: string, pass: string) => Promise<ProfileData | null>;
  signup: (values: SignupData) => Promise<void>;
  loginWithGoogle: () => Promise<ProfileData | null>;
  sendPasswordReset: (email: string) => Promise<void>;
  logout: () => void;
  isLoading: boolean;
  updateUserProfile: (data: Partial<ProfileData>, newPhotoFile: File | null) => Promise<void>;
  upgradeToPro: () => Promise<void>;
  getAllUsers: (role?: UserRole) => Promise<ProfileData[]>;
  getUserTestHistory: (userId: string) => Promise<TestResult[]>;
  getUserProfile: (userId: string) => Promise<ProfileData | null>;
  approveTeacher: (teacherId: string) => Promise<void>;
  getCompletedGameLevels: () => Promise<number[]>;
  saveCompletedGameLevel: (levelId: number) => Promise<void>;
}

export const AuthContext = createContext<AuthContextType | undefined>(undefined);

const googleProvider = new GoogleAuthProvider();
googleProvider.setCustomParameters({
  prompt: 'select_account'
});
const ADMIN_EMAILS = ['scm098@gmail.com', 'pallavib202@gmail.com', 'myabacuspro@gmail.com'];

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [profile, setProfile] = useState<ProfileData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const auth = getAuth(firebaseApp);
  const firestore = getFirestore(firebaseApp);
  const storage = getStorage(firebaseApp);
  
  const fetchProfile = useCallback(async (user: User): Promise<ProfileData | null> => {
    try {
      const userDocRef = doc(firestore, 'users', user.uid);
      const userDoc = await getDoc(userDocRef);
      if (userDoc.exists()) {
        const profileData = userDoc.data() as ProfileData;
        if (profileData.isAdmin) {
          profileData.subscriptionStatus = 'pro';
          profileData.role = 'admin';
        }
        setProfile(profileData);
        return profileData;
      }
      return null;
    } catch (error) {
      console.error('Error fetching profile:', error);
      return null;
    }
  }, [firestore]);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      setIsLoading(true);
      setUser(user);
      if (user) {
        await fetchProfile(user);
      } else {
        setProfile(null);
      }
      setIsLoading(false);
    });
    return () => unsubscribe();
  }, [auth, fetchProfile]);

  const login = useCallback(async (email: string, pass: string): Promise<ProfileData | null> => {
    const userCredential = await signInWithEmailAndPassword(auth, email, pass);
    return await fetchProfile(userCredential.user);
  }, [auth, fetchProfile]);
  
  const signup = useCallback(async (values: SignupData) => {
      const userCredential = await createUserWithEmailAndPassword(auth, values.email, values.password);
      const user = userCredential.user;
      
      let photoURL = '';
      if (values.profilePhoto && typeof values.profilePhoto !== 'string' && values.profilePhoto.length > 0) {
        const file = values.profilePhoto[0];
        const storageRef = ref(storage, `profile_photos/${user.uid}`);
        await uploadBytes(storageRef, file);
        photoURL = await getDownloadURL(storageRef);
      }

      await updateProfile(user, {
          displayName: `${values.firstName} ${values.surname}`,
          photoURL: photoURL
      });

      const userDocRef = doc(firestore, 'users', user.uid);
      const { password, confirmPassword, profilePhoto, ...profileData } = values;
      
      const isAdmin = ADMIN_EMAILS.includes(user.email || '');
      const role = isAdmin ? 'admin' : values.role;

      await setDoc(userDocRef, {
        ...profileData,
        uid: user.uid,
        email: user.email,
        profilePhoto: photoURL || '',
        createdAt: serverTimestamp(),
        subscriptionStatus: 'free',
        role: role,
        isAdmin: isAdmin,
        ...(role === 'teacher' && { status: 'pending' }),
        ...(role === 'student' && { teacherId: values.teacherId || null }),
      });

      setUser(auth.currentUser); // refresh user state
      await fetchProfile(user);

  }, [auth, firestore, storage, fetchProfile]);

  const loginWithGoogle = useCallback(async (): Promise<ProfileData | null> => {
    const result = await signInWithPopup(auth, googleProvider);
    const user = result.user;
    const userDocRef = doc(firestore, 'users', user.uid);
    const userDoc = await getDoc(userDocRef);

    if (!userDoc.exists()) {
      const [firstName, ...rest] = (user.displayName || '').split(' ');
      const surname = rest.pop() || '';
      const middleName = rest.join(' ');
      const isAdmin = ADMIN_EMAILS.includes(user.email || '');
      
       await setDoc(userDocRef, {
        uid: user.uid,
        email: user.email,
        firstName: firstName || '',
        surname: surname,
        middleName: middleName,
        profilePhoto: user.photoURL || '',
        createdAt: serverTimestamp(),
        subscriptionStatus: 'free',
        role: isAdmin ? 'admin' : 'student', // Default to student
        isAdmin: isAdmin,
      });
    }
    return await fetchProfile(user);
  }, [auth, firestore, fetchProfile]);
  
  const sendPasswordReset = useCallback(async (email: string) => {
    await sendPasswordResetEmail(auth, email);
  }, [auth]);

  const updateUserProfile = useCallback(async (data: Partial<ProfileData>, newPhotoFile: File | null) => {
    if (!user) {
      throw new Error("User not logged in");
    }

    console.log('Starting profile update...', {
      hasNewPhoto: !!newPhotoFile,
      photoFileName: newPhotoFile?.name,
      photoSize: newPhotoFile?.size,
      dataKeys: Object.keys(data)
    });

    try {
      const profileDataToUpdate: Partial<ProfileData> = { ...data };
      const authUpdateData: { displayName?: string; photoURL?: string } = {};

      // Step 1: Upload photo if a new one is provided
      if (newPhotoFile) {
        console.log('Uploading new photo...', {
          fileName: newPhotoFile.name,
          size: newPhotoFile.size,
          type: newPhotoFile.type
        });

        // Validate file before upload
        if (!newPhotoFile.type.startsWith('image/')) {
          throw new Error('Invalid file type. Please select an image file.');
        }

        if (newPhotoFile.size > 10 * 1024 * 1024) { // 10MB limit
          throw new Error('File is too large. Please select an image smaller than 10MB.');
        }

        try {
          // Create a unique filename with timestamp to avoid caching issues
          const timestamp = Date.now();
          const fileExtension = newPhotoFile.name.split('.').pop() || 'jpg';
          const fileName = `${user.uid}_${timestamp}.${fileExtension}`;
          
          const storageRef = ref(storage, `profile_photos/${fileName}`);
          
          console.log('Uploading to Firebase Storage...', { path: `profile_photos/${fileName}` });
          
          // Upload with metadata
          const uploadResult = await uploadBytes(storageRef, newPhotoFile, {
            contentType: newPhotoFile.type,
            customMetadata: {
              userId: user.uid,
              originalName: newPhotoFile.name,
              uploadedAt: new Date().toISOString()
            }
          });

          console.log('Upload successful, getting download URL...');
          
          const photoURL = await getDownloadURL(storageRef);
          
          console.log('Download URL obtained:', photoURL);

          profileDataToUpdate.profilePhoto = photoURL;
          authUpdateData.photoURL = photoURL;

        } catch (uploadError: any) {
          console.error('Photo upload failed:', uploadError);
          throw new Error(`Failed to upload photo: ${uploadError.message}`);
        }
      }

      // Step 2: Prepare display name for Auth profile update
      if (data.firstName && data.surname) {
        authUpdateData.displayName = `${data.firstName} ${data.surname}`;
      }

      // Step 3: Update Firebase Auth profile if there are changes
      if (Object.keys(authUpdateData).length > 0) {
        console.log('Updating Firebase Auth profile...', authUpdateData);
        try {
          await updateProfile(user, authUpdateData);
          console.log('Firebase Auth profile updated successfully');
        } catch (authError: any) {
          console.error('Failed to update Firebase Auth profile:', authError);
          // Don't throw here, continue with Firestore update
        }
      }
      
      // Step 4: Update Firestore document with timestamp
      console.log('Updating Firestore document...', profileDataToUpdate);
      
      const userDocRef = doc(firestore, 'users', user.uid);
      const updateData = {
        ...profileDataToUpdate,
        updatedAt: serverTimestamp()
      };
      
      await setDoc(userDocRef, updateData, { merge: true });
      console.log('Firestore document updated successfully');

      // Step 5: Refresh local profile state
      console.log('Refreshing profile state...');
      await fetchProfile(user);
      console.log('Profile update completed successfully');

    } catch (error: any) {
      console.error('Profile update error:', error);
      throw new Error(error.message || 'Failed to update profile. Please try again.');
    }
  }, [user, firestore, storage, fetchProfile]);

  const upgradeToPro = useCallback(async () => {
    if (!user) throw new Error("User not logged in");
    const userDocRef = doc(firestore, 'users', user.uid);
    await setDoc(userDocRef, { subscriptionStatus: 'pro' }, { merge: true });
    await fetchProfile(user);
  }, [user, firestore, fetchProfile]);

  const logout = useCallback(async () => {
    await signOut(auth);
  }, [auth]);
  
  const getAllUsers = useCallback(async (role?: UserRole): Promise<ProfileData[]> => {
      const usersCol = collection(firestore, 'users');
      let q = query(usersCol);
      if (role) {
          q = query(usersCol, where("role", "==", role));
      }
      const userSnapshot = await getDocs(q);
      const userList = userSnapshot.docs.map(doc => doc.data() as ProfileData);
      return userList;
  }, [firestore]);

  const getUserTestHistory = useCallback(async (userId: string): Promise<TestResult[]> => {
     if (profile?.isAdmin || profile?.role === 'teacher') {
        const q = query(collection(firestore, 'testResults'), where("userId", "==", userId));
        const querySnapshot = await getDocs(q); 
        const history: TestResult[] = [];
        querySnapshot.forEach((doc) => {
            const data = doc.data();
            history.push({
                id: doc.id,
                ...data,
                createdAt: data.createdAt.toDate(),
            } as TestResult);
        });
        return history;
     }
     return [];
  }, [profile, firestore]);

  const getUserProfile = useCallback(async (userId: string): Promise<ProfileData | null> => {
     if (profile?.isAdmin || profile?.role === 'teacher') {
        const userDocRef = doc(firestore, 'users', userId);
        const userDoc = await getDoc(userDocRef);
        return userDoc.exists() ? userDoc.data() as ProfileData : null;
     }
     return null;
  }, [profile, firestore]);
  
  const approveTeacher = useCallback(async (teacherId: string) => {
      if (profile?.isAdmin) {
          const teacherRef = doc(firestore, 'users', teacherId);
          await setDoc(teacherRef, { status: 'approved' }, { merge: true });
      } else {
          throw new Error('Only admins can approve teachers.');
      }
  }, [profile, firestore]);

  const getCompletedGameLevels = useCallback(async (): Promise<number[]> => {
    if (!user) return [];
    const docRef = doc(firestore, "gameProgress", user.uid);
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      return docSnap.data().completedLevels || [];
    }
    return [];
  }, [user, firestore]);

  const saveCompletedGameLevel = useCallback(async (levelId: number) => {
    if (!user) return;
    const docRef = doc(firestore, "gameProgress", user.uid);
    await setDoc(docRef, {
      completedLevels: arrayUnion(levelId)
    }, { merge: true });
  }, [user, firestore]);

  const value = { user, profile, login, signup, loginWithGoogle, logout, isLoading, updateUserProfile, upgradeToPro, sendPasswordReset, getAllUsers, getUserTestHistory, getUserProfile, approveTeacher, getCompletedGameLevels, saveCompletedGameLevel };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}
